* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.logo {
  width: 100%;
}

.openModal {
  margin-bottom: 20px;
}

.ava {
  max-height: 105px;
  max-width: 105px;
  margin-right: 15px;
}

.pending {
  filter: opacity(50%);
}

.card {
  position: relative;
  display: flex;
  flex-shrink: 0;
  height: 120px;
  width: 385px;
  border-radius: 4px;
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 8px;
  border: 1px solid rgb(229, 233, 242);
  border-left: 4px solid green;
}

.loadImg {
  max-width: 100%;
  max-height: 100px;
}

.Fields {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
}

.Fields:first-of-type {
  margin-top: 30px;
}

.sbmt {
  display: block;
  margin-left: auto;
}

.buttonAdd {
  display: block;
  margin-left: auto;
}

.App {
  height: 100vh;
  display: flex;
}

.fileI {
  display: none;
}

.Online {
  position: fixed;
  right: 20px;
  top: 10px;
  color: #0a830a;
}

.Offline {
  position: fixed;
  right: 20px;
  top: 10px;
  color: #801717;
}

.emptyA {
  width: 30px;
  height: 30px;
  background: #000;
}

.leftSide {
  padding: 20px 10px;
  flex-shrink: 0;
  width: 180px;
  height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  background: linear-gradient(
          180deg
          ,#ffffff 0%,#e6eefb 50%,#2b5499 100% );
}

.rightSide {
  flex-grow: 1;
  padding: 40px 20px;
  overflow: auto;
}

@media (max-width: 600px) {
  .leftSide {
    display: none;
  }

  .card {
    width: 100%;
    height: auto;
    min-height: 120px;
  }
}
